import React from 'react';
import { Table } from 'react-bootstrap';
import { connect } from 'react-redux';

function Cart(props) {
  return (
    <div>
      <Table striped bordered hover variant="dark">
        <thead>
          <tr>
            <th>#</th>
            <th>상품명</th>
            <th>수량</th>
            <th>변경</th>
          </tr>
        </thead>
        <tbody>
          {
            props.quan.map((item, i)=>{
              return (
                <tr>
                  <td>{ i + 1 }</td>
                  <td>{ item.name }</td>
                  <td>{ item.quan }</td>
                  <td>
                    <button onClick={()=>{ props.dispatch({ type: 'increase', index: i }) }}>+</button>
                    <button onClick={()=>{ props.dispatch({ type: 'decrease', index: i }) }}>-</button>
                  </td>
                </tr>
              )
            })
          }
        </tbody>
      </Table>

      {
        props.showAlert
        ? (
          <div className="my-alert">
            <p>지금 구매하시면 추가할인 20%</p>
            <button onClick={()=>{ props.dispatch({ type: false }) }}>닫기</button>
          </div>
        )
        : null

      }

    </div>
  )
}

function conn(state) {
  
  return {
    quan: state.reducer,
    showAlert: state.reducerShowAlert
  }
}

export default connect(conn)(Cart)
