import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';


let showAlert = true;

function reducerShowAlert(state = showAlert, action) {
  return action.type;
}

let defaultState = [
  // { id: 1, name: '멋진신발', quan: 2 },
  // { id: 2, name: '빨간휴지', quan: 1 },
  // { id: 3, name: '파란휴지', quan: 3 }
];

function reducer(state = defaultState, action) {
  let copy = [...state];
  let i = action.index;

  if ( action.type === 'addCart') {
    let thisGoods = copy.find(item => item.id === action.payload.id);
    if (thisGoods === undefined) {
      copy.push(action.payload);
    } else {
      thisGoods.quan++;
    }
  } else if (action.type === 'increase') {
    copy[i].quan++;
  } else if (action.type === 'decrease') {
    if (0 < copy[i].quan) {
      copy[i].quan--;
    }
  }
  return copy;
}

let store = createStore(combineReducers({reducer, reducerShowAlert}));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
